import { configure } from '@storybook/preact';
import '../src/i18nline-glue';
import '../src/style';
import { addDecorator } from '@storybook/preact';
import { Header } from '../src/components/header';
import { Menu } from '../src/containers/Menu';
import { AppContext } from '../src/utils/app.context';
import { text } from '@storybook/addon-knobs';

addDecorator(storyFn => (
    <AppContext.Provider value={{nodeHostname: text('nodeHostname', 'ql-anaymarcos'), menuEnabled:true }}>
        <Header Menu={Menu}></Header>
        <div id="content">
            {storyFn()}
        </div>
    </AppContext.Provider>
))

configure([
    require.context('../stories', true, /\.stories\.js$/),
    require.context('../plugins', true, /\.stories\.js$/)
], module);
